main {
    display: flex;
    justify-content: center;
    position: relative;
    padding-top: 350rem;
    background-color: rgb(250, 250, 250);
    padding-bottom: 30rem;
}

main::before {
    content: "";
    display: block;
    position: absolute;
    width: calc(100% - 100rem);
    top: 0;
    height: calc(100vh - 150rem);
    margin: 50rem;
    margin-top: 100rem;
    border-radius: 30rem;
    background-image: url("./imgs/dynamic_page/htb_background.svg");
}

main::after {
    content: "";
    display: block;
    position: absolute;
    width: 100%;
    top: 0;
    margin: 0 auto;
    margin-top: 150rem;
    width: 150rem;
    aspect-ratio: 1;
    border-radius: 50%;
    background-image: url("./imgs/dynamic_page/usage_icon.webp");
    background-size: contain;
    border: 10rem solid rgba(255,255,255,0.1);
    background-repeat: no-repeat;
}